* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.nav-list {
  height: 10vh;
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
}

.home,
.about {
  height: 90vh;
  position: absolute;
  width: 100%;
}
.home-content,
.about-content {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
  img {
    height: 70%;
  }
  h1 {
    font-size: 50px;
    margin: 30px 0;
  }
  p {
    font-size: 20px;
  }
}

.home {
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
}
.about {
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}
